/*  MODULE: WIDGETS TOP RIGHT
//////////////////////////////////////////////////////////////////////////////*/

.module--widgets-topright {
    display: grid;
    place-items: center end;
}
.widgets-topright {
    display: flex;
    flex-wrap: wrap;
    justify-content: end;
    gap: var(--m);
}

.github {
    font-size: 0;
    line-height: 0;
    width: 2rem;
    height: 2rem;
    display: block;
    background-image: url("../../../images/icons/icon--github-dark.svg");
    background-size: 1.5rem;
    background-repeat: no-repeat;
    background-position: center;
}
